.App {
  text-align: center;
  width: 50%;
  min-width: 600px;
  margin: auto;
  font-family: 'Orbitron', sans-serif;
}

.App h2 {
  font-family: Arial, Helvetica, sans-serif;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
